份是最高管理员。 ")])]),_c('van-card',{attrs:{"title":"公共线索10"}},[_c('div',{attrs:{"slot":"desc"},slot:"desc"},[_vm._v(" 何子安的电脑里有2张PS的保险单，都是100买500万保险的。受益人一张填的刘克名字，另一张填的马晓光名字。 ")])]),_c('van-card',{attrs:{"title":"公共线索11"}},[_c('div',{attrs:{"slot":"desc"},slot:"desc"},[_vm._v(" 何子安身上有一个小u盘，打开一看，里边是一个压缩包，压缩包名字写的“魔力闪闪亮”。但有密码，打不开。 ")])]),_c('van-card',{attrs:{"title":"公共线索12"}},[_c('div',{attrs:{"slot":"desc"},slot:"desc"},[_vm._v(" 何子安在×江文学网的用户名是“宅男好可乐”，他收藏了一本《残照又当楼》的小说，还给小说作者“雨慢漫”打赏过不少钱。 ")])]),_c('van-card',{attrs:{"title":"公共线索13"}},[_c('div',{attrs:{"slot":"desc"},slot:"desc"},[_vm._v(" 何子安的遗物里发现了一张他的白血病化验单，结果是阳性。而且已经是晚期。还有，何子安带着一块卡西欧G-SHOCK表。 ")])])],1):_c('van-button',{staticClass:"btn",attrs:{"color":"#FFB700","round":""},on:{"click":function($event){return _vm.levelup(5)}}},[_vm._v("解锁线索")])],1)],1)],1),_c('van-popup',{style:({ height: '90%' }),attrs:{"position":"bottom"},model:{value:(_vm.isShowEnd),callback:function ($$v) {_vm.isShowEnd=$$v},expression:"isShowEnd"}},[(_vm.level > 5)?_c('van-tabs',{model:{value:(_vm.activeEnd),callback:function ($$v) {_vm.activeEnd=$$v},expression:"activeEnd"}},[_c('van-tab',{staticClass:"content",attrs:{"title":"人物"}},[_vm._v("NPC1男"),_c('br'),_vm._v(" 何子安 男 28岁厦门大学物理系讲师(死者)"),_c('br'),_c('br'),_vm._v(" 玩家3男3女 "),_c('br'),_c('br'),_vm._v(" 马晓光 男 29岁 家族企业继承人（代驾司机）"),_c('br'),_vm._v(" 刘克 男 28岁 国防单位计算机安防员（网吧网管）"),_c('br'),_vm._v(" 段大鹏 男 29岁领导人营养师（小餐馆厨子）"),_c('br'),_vm._v(" 徐如雨 女 28岁 著名编剧（名不见经传女频网文作家）"),_c('br'),_vm._v(" 莫丽 女 28岁 ladyface酒吧特约歌手（网络小主播） "),_c('br'),_vm._v(" 张萱萱 女 29岁 对接明星高级化妆师（入殓师） ")]),_c('van-tab',{staticClass:"content",attrs:{"title":"推凶理由"}},[_vm._v(" 说明推出何子安被徐如雨杀的理由是 "),_c('br'),_c('br'),_vm._v(" 【线索】徐如雨的购买记录显示，她自2013年开始，每过一段时间就会购买一些苯丙胺。但在2014年后就没有再买过了。 "),_c('br'),_c('br'),_vm._v(" 说明徐如雨之前经常服用兴奋剂，后来不服用了，说明她有了强烈的耐药性，应该换更强药效的东西了（何子安给的冰毒 徐如雨拍了自己很多毒瘾犯了的照片）。在前半部分的剧本中，她开始和大家一样玩的很HIGH，但到结尾的时候她的思路很清晰。所以徐如雨没有最后3条的片段性记忆，而是相对完整的一些记忆。所以徐如雨描述后边的关键词记忆时，会和大家脑海里的有一定偏差（如果徐如雨是高手则会隐瞒的很好） "),_c('br'),_c('br'),_vm._v(" （何子安邀请大家去HIGH，在段大鹏的视角里，徐如雨是和何子安说了几句话的，大家问徐如雨说什么话，玩徐如雨的玩家也许会说出“何子安。张萱萱”的全名）这是大家片段性的记忆里，徐如雨是第一个说全3个字名字的人。也就是说她的思路比一般人清晰。 "),_c('br'),_c('br'),_vm._v(" 其实徐如雨当时已经清醒，她是在问何子安要他许诺给自己的冰毒。何子安的意思是你跟着我来才给你，所以徐如雨才上车。但徐如雨肯定不会说这个事。只能用其他话来搪塞。 "),_c('br'),_c('br'),_vm._v(" 另外就是前边组织者说过何子安的包是空的，那么作为“绝命毒师”的何子安，是很有可能带着一包毒品的，但包却是空的，就说明有人拿走了他的包里东西。如果能推出徐如雨有毒瘾，这里很容易把矛头指向她。何子安包里的毒品足够徐如雨吸十年（作者请教过警察，冰毒每次大约吸0.5克，5天吸一次。一年36.5克，10年不到400克，而且冰毒属于化学品，只要在冰箱里干燥储存，可以存放很久很久。） 另外就是，大家混得都不如意，段大鹏，刘克，马晓光是看在和何子安的“兄弟情”才来的。莫丽是被何子安要挟公布脱衣舞视频胁迫才来的。而同样混的不好的徐如雨为什么会来?她能没把柄在何子安手上?（在张萱萱本里有说莫丽和徐如雨开始都不想来，但是后来突然表示要来其实就是被何子安胁迫了）"),_c('br'),_c('br'),_vm._v(" 何子安是学化学的，他长期接触化学物质，导致了白血病。而且他们大学丢失了一些实验用的化学品和器具。正是何子安用来做TNT炸药了，用浓硝酸和浓硫酸混合，冷却后加入甲苯，用水浴蒸馏装置加热蒸馏提取TNT炸药。"),_c('br'),_c('br'),_vm._v(" 何子安物理很好，他之前经常去东坪山（张萱萱故事里有提到）爬山，早就知道有一块大石头在公路上方的山头。他用炸药做了定时炸弹，用物理知识可以计算出石头一旦失去支撑力后的下落速度。同时也能推导出落点和准确时间，他只要在临界值到达公路入口匀速开车运动就可以被石头砸中。然后接来下的暴雨会冲刷掉痕迹。"),_c('br'),_c('br'),_vm._v(" 另外，就是何子安之前发现得了白血病，但他知道如果自己坦白，张萱萱一定不会放弃救治他。何子安被安娜一直胁迫制毒，所以他卖了自己的房产，弄了一个最大的意外险。本想将自己和安娜还有欺负过他的同学一波带走。留给张萱萱巨额保险金。但不料喝醉后的张萱萱非要和同学们坐一个车，何子安为了张萱萱饶过了其他人，只让自己和安娜的车被石头砸。"),_c('br'),_c('br'),_vm._v(" 但人算不如天算，何子安并没有实验过TNT炸药的威力，爆破炸下来的石头有了裂缝，在山体上撞击碎裂了，只有一些小块击中了他的车。 警察给出的信息是“被山上滚落的碎石块砸中，车上有1人不幸被砸中头颅当场死亡”，注意是“碎石块”。"),_c('br'),_c('br'),_vm._v(" 但警察给出的信息是“像是一起意外事故”，而安娜的死明显不是意外事故，就说明肯定有真凶。"),_c('br'),_c('br'),_c('br'),_c('br')]),_c('van-tab',{staticClass:"content",attrs:{"title":"人物关系"}},[_vm._v(" 大家都是高中时期的同学2008届华美高中毕业。"),_c('br'),_c('br'),_vm._v(" 何子安高中时期个头比较低，性格内向，但学习很好，尤其是数理化，次次都几乎可以达到满分，老师都说何子安是未来的科学家。他和马晓光和刘克关系比较好。 段大鹏高中时期是有名的班霸，总是欺负班里同学，尤其爱欺负老实的何子安。还在徐如雨的灵感启迪下给何子安起了外号“河狸”。何子安想向段大鹏示好，主动想和段大鹏学习打篮球，但段大鹏让何子安从自己胯下钻过去。何子安不钻，段大鹏恼羞成怒又打了何子安。"),_c('br'),_c('br'),_vm._v(" 刘克爱逃课玩电脑打游戏很缺钱，总问何子安借钱。何子安开始一直将他当朋友，但是刘克遇到校外的社会青年要钱，刘克为了自保竞把何子安出卖，让那些社会青年把何子安带走打了一顿并抢了钱。"),_c('br'),_c('br'),_vm._v(" 马晓光向徐如雨表白被拒，他也想看何子安出丑，便故意撮合何子安和莫丽，结果导致莫丽在全班同学面前公然羞辱何子安。他还把徐如雨父亲的照片撕碎了偷偷放进何子安的书包嫁祸他。"),_c('br'),_c('br'),_vm._v(" 徐如雨是学习委员，不喜欢何子安，因为何子安总比自己成绩好，总压她一头。她还向老师告状说何子安为了逃避上体育课用石灰袋加水增加额头和温度计温度。导致何子安被通报批评罚做俯卧撑，在全班人面前丢脸。何子安想和徐如雨搞好关系，把马晓光撕碎的照片PS好还给了徐如雨，但徐如雨反而冤枉何子安拿了她爸爸的照片，并且公然扇他耳光。"),_c('br'),_c('br'),_vm._v(" 莫丽上体育课时意外从双杠摔下，昏迷了过去。同学们都送来礼品慰问，何子安折了一夜千纸鹤，折了999只，第1000只的纸上写下来（希望莫丽快点康复的心愿）送她了第1000只千纸鹤，让她打开。结果被醒来的莫丽当着他面扔进垃圾桶。(只要折好999 只千纸鹤，第1000只就可以许愿成功，何子安的母亲当年心脏病病发，何子安为母亲折到966只千纸鹤时母亲去世）"),_c('br'),_c('br'),_vm._v(" 一直被人欺负，一直没有真心朋友的何子安心里逐渐扭曲。但这时候的他还没有杀人的念头。他怕被欺负，怕被疏远，所以何子安尽全力维护着他拥有的“友情（马晓光和刘克）”，也拼尽全力去讨好段大鹏，还将母亲的遗物吉他送给了段大鹏。"),_c('br'),_c('br'),_vm._v(" 其实对比起段大鹏，他更恨刘克和马晓光，连张萱萱都看不惯段大鹏欺负何子安的事情出来主持过公道。但这2个人自诩是他的好朋友，却在他受到段大鹏欺负的时候没有站出来为何子安说过一句话。等段大鹏走了，刘克和马晓光又继续和何子安称兄道弟。"),_c('br'),_c('br'),_vm._v(" 但何子安是一个十分内向，并且心有城府的人，他从来不会在同学面前显露出他的恨意。"),_c('br'),_c('br'),_vm._v(" 毕业后何子安考入了名牌大学，人也变帅了，是同学中混的最好的一个。他开始尝试性的做一些报复。"),_c('br'),_c('br'),_vm._v(" 他故意给同学们都说自己混的很好，意图就是让大家羡慕。故意追求张萱萱，拍了和张萱萱亲密的照片只对段大鹏朋友圈可见，故意气段大鹏。"),_c('br'),_c('br'),_vm._v(" 他气气段大鹏的同时，还要表现出很无辜的样子，还对段大鹏嘘寒问暖，让段大鹏有火没处发。"),_c('br'),_c('br'),_vm._v(" 至于他最恨的刘克马晓光，他利用自己数学方面的天份写了庞氏骗局的公式和外国的骗子合作，骗了马晓光钱再拉他一把，让马晓光感激涕零。还借钱给刘克开网吧，但他见到刘克生意不错，就故意戳弄马晓光去放火，导致刘克一直欠着他的。"),_c('br'),_c('br'),_vm._v(" 何子安把刘克马晓光骗的一愣一愣，他还PS了保险公司的意外险保险单受益人填写了刘克马晓光的名字，让二人深受感动，也买了多份保险填下了何子安的名字。"),_c('br'),_c('br'),_vm._v(" 因为刘克和马晓光买保险属于随机事件。何子安这时候其实也没动杀心。"),_c('br'),_c('br'),_vm._v(" 另外，莫丽和徐如雨他也没有放过。"),_c('br'),_c('br'),_vm._v(" 他冒充徐如雨写的小说的读者，给徐如雨打赏，取得信任后劝徐如雨服用毒品来激发创作灵感，成功的让徐如雨染上了毒瘾。"),_c('br'),_c('br'),_vm._v(" 这个毒品是何子安利用自己的化学天赋从【新康泰克感冒药】里含的麻黄碱提取出来的冰毒。"),_c('br'),_c('br'),_vm._v(" 何子安利用之前的人脉开设私彩网站，骗光莫丽的钱，再放贷给她，让莫丽拍下脱衣舞视频来偿还。"),_c('br'),_c('br'),_vm._v(" 何子安利用毒品控制了徐如雨，利用借贷控制了莫丽。但他的心里还是很空虚。"),_c('br'),_c('br'),_vm._v(" 他在酒吧买醉的时候遇到了“知己”安娜。其实安娜是当地黑社会老大的女人，她会催眠术，从何子安那里套出了何子安会制毒的本领。"),_c('br'),_c('br'),_vm._v(" 于是安娜半拉拢半逼迫何子安成了他们的摇钱树。何子安负责制毒，黑社会老大的亲信“鬼哥”负责运毒和交易。"),_c('br'),_c('br'),_vm._v(" 鬼哥为了掩人耳目，经常把毒品藏在公仔玩具里，他为人谨慎从未被抓住过。"),_c('br'),_c('br'),_vm._v(" 但毕竟树大招风，鬼哥的名气传了出去，被公安局悬赏通缉。所以有些人曾经在通缉令上看到过鬼哥的照片。（他鼻头有个黑痣）"),_c('br'),_c('br'),_vm._v(" 有个人（酱油NPC是谁不重要）突发奇想，心想只要能提供线索，公安局就会给奖金，他便故意在电线杆上贴了【寻人启事】故意说鬼哥是自己精神分裂的哥哥，还说鬼哥有暴力倾向，让大家不要和他说话，只需要暗暗的把鬼哥的行踪电话告诉自己就行。"),_c('br'),_c('br'),_vm._v(" 何子安的父亲被醉酒渣土车司机撞死，何子安提前给父亲买了保险，但保险公司为了节省保险金抠字眼，说醉酒不属于理赔范围，何子安气不过和他们打官司，结果输了。"),_c('br'),_c('br'),_vm._v(" 这时候的何子安就想到了骗保，因为之前马晓光和刘克刚好买了保险，何子安动了杀心，心想杀了你们我不光复了仇，还能骗保。所以他一直在酝酿机会。"),_c('br'),_c('br'),_vm._v(" 何子安的父亲的脸庞被车祸撞的支离破碎，是张萱萱把何子安的父亲拼起来复原的。何子安深受感动，他又联想起张萱萱从未欺负过他，而且还在段大鹏欺负何子安时打抱不平，所以这时候的何子安是真的爱上张萱萱了。他想金盆洗手和张萱萱结婚。"),_c('br'),_c('br'),_vm._v(" 但黑社会老大和安娜不同意，他们怕何子安洗白上岸后断了财路，所以他们一直在逼迫何子安。结婚的日期就一直拖着。"),_c('br'),_c('br'),_vm._v(" 就在这时候，何子安发现自己得了白血病晚期（经常接触化学品）。"),_c('br'),_c('br'),_vm._v(" 幼年丧母，上学时被人羞辱欺负，朋友的虚情假意，以及父亲的死，保险公司的卑鄙无耻，外加安娜的贪婪，他的心里已经扭曲了。在他的眼里，这个社会只有张萱萱一个好人，其他人都该死。"),_c('br'),_c('br'),_vm._v(" 他终于想好了报复计划，他利用浓硫酸硝酸提纯制造了三硝基甲苯（TNT炸药），制作了定时炸弹，然后在东坪山的一块巨石下埋好了炸弹，设置好起爆时间。利用他的物理天赋计算出巨石的落点。"),_c('br'),_c('br'),_vm._v(" 然后他卖了房，买了300万元的意外险，保险金足足有十亿。收益人写的张萱萱。一来让张萱萱下半生不愁钱，二来要报复保险公司。"),_c('br'),_c('br'),_vm._v(" 何子安故意和安娜示好，说他想通了，只有贩毒才能让他发财，故意给安娜说他和张萱萱好是为了报复段大鹏。还说要设局砸死张萱萱骗保。让安娜帮助他。 安娜当然同意，但其实何子安的真正目标是安娜，他早就准备了安眠药，放在了安娜的饮料里。就是为了到时候偷梁换柱。"),_c('br'),_c('br'),_vm._v(" 同时，何子安也要将欺负过他的那些同学一波带走。所以他故意借口同学聚会，还逼迫徐如雨和莫丽必须来。到时候由安娜将他们催眠，这样何子安就能把他们都安排在一辆车上。"),_c('br'),_c('br'),_vm._v(" 但何子安没想到张萱萱窃听到了他和安娜的话，张萱萱以为何子安一直没有爱过自己，还设计要砸死自己骗保，她后悔万分，转念想到可以将计就计。知道何子安布的这个陷阱非常依赖时间，而且何子安一直带着一块非常准时的表。她故意调整了何子安的表，目的就是让落石砸在何子安自己头上。谁料何子安带着的是电波自动对时表，张萱萱的操作完全落空。"),_c('br'),_c('br'),_vm._v(" 安娜催眠需要服用一些辅助性药品，她让何子安去她那里拿放在心理咨询室的辅助药品，但何子安拿错成了兴奋剂。"),_c('br'),_c('br'),_vm._v(" 兴奋剂导致大家被催眠后并没有失去自主意识，而是成了一群不受控制的人。"),_c('br'),_c('br'),_vm._v(" 何子安努力把大家聚拢在一起，想着等到4点时一波带走。但大家分别闹出了不少乱子，还把何子安的下线“鬼哥”给抓住了。"),_c('br'),_c('br'),_vm._v(" 何子安怕鬼哥认出他，当时就借口上厕所藏了起来。等他回来，鬼哥已经被马晓光刘克送到了警察局。而鬼哥被大家按在地上的时候，手一甩（某人剧本里有写），将装有毒品的哆啦A梦摔到了一边，被徐如雨捡到。"),_c('br'),_c('br'),_vm._v(" 徐如雨看到饭店供桌上的关公，她心想拜关公又不能要什么来什么，但哆啦A梦可以啊!于是就好心换了哆啦A梦上去。将饭店的关公自己装到了包里。"),_c('br'),_c('br'),_vm._v(" 马晓光和刘克回来，何子安怕大家再走散，就张罗着大家一起去HIGH，他把安娜迷晕，抱上车，然后让大家一起上车，又借口车坐不下，让张萱萱打车回家。（徐如雨本里有些详细过程因为徐如雨以前经常服用兴奋剂有了抗性提前清醒）"),_c('br'),_c('br'),_vm._v(" 但服用了兴奋剂的张萱萱非要和大家一起去。她这个无意的举动成了拯救大家的天使。"),_c('br'),_c('br'),_vm._v(" 何子安眼看时间快到了，他不能再磨蹭，于是他为了张萱萱的安全打算饶了大家一命，只带着安娜去踩自己布下的自杀陷阱。让大家坐后车，自己开前车。虽然张萱萱调了何子安的表，但何子安的表自动对时，这时候早就是正常时间了。"),_c('br'),_c('br'),_vm._v(" 但是何子安低估了TNT炸药的威力，导致石头被炸裂，在山体上碰撞后解体成碎石，其中一块碎石砸穿了挡风玻璃，但是并未砸死何子安和安娜。"),_c('br'),_c('br'),_vm._v(" 这时候大家的后车追尾到前车，何子安看到徐如雨出来拿走他包里的毒品，他故意大喊起来，目的就是逼迫徐如雨砸死他，不管警察是否查的出来徐如雨是凶手，他这个都属于意外身亡。张萱萱还是可以得到理赔金。"),_c('br'),_c('br'),_vm._v(" 徐如雨砸死何子安后，安娜也醒了，她想逃跑，但被徐如雨追上砸死，抛尸悬崖（这里就是为了给大家说明何子安和安娜不是被意外砸死的）"),_c('br'),_c('br'),_vm._v(" 然后徐如雨藏好抢来的毒品，回到车上装晕。等待救援。"),_c('br'),_c('br'),_vm._v(" 然后，作品的名字叫【醉后一夜】，其实也就是“最后一夜”。这是一个反校园暴力的本，有些人自认为开玩笑，嘻嘻哈哈过去的事情，在受害者心里却是一道永远都抹不平的伤痕。"),_c('br'),_c('br'),_vm._v(" 你永远不知道那些被你欺凌的人会怎样报复你。"),_c('br'),_c('br')]),_c('van-tab',{staticClass:"content",attrs:{"title":"案件时间线"}},[_vm._v(" 晚上8点，大伙儿到了酒吧见面。"),_c('br'),_c('br'),_vm._v(" 大家喝酒时【安娜】来了，给6个人（除了何子安）进行了催眠，但何子安把药放错了，放成了兴奋剂。"),_c('br'),_c('br'),_vm._v(" 大家喝的越来越高兴，开始了一系列精彩的表演。"),_c('br'),_c('br'),_vm._v(" 9点整，大家一起离开了酒吧，恰好来了个公交车，大家一起上了这趟公交车，但没有带零钱，一个老奶奶帮大家投了钱。在车上遇到了小偷，段大鹏大喝一声:“我是警察叔叔”挺身而出，被盗匪划破了大腿后跳窗逃跑。"),_c('br'),_c('br'),_vm._v(" 9点15，老奶奶（赵奶奶）陪着几个人下车找到了一家宠物医院【爱心宝贝诊所】，段大鹏很感激老奶奶，认她做了奶奶。"),_c('br'),_c('br'),_vm._v(" 进去后要医生给段大鹏包扎，结果马晓光把诊所里的哈士奇当做狼给痛打了一顿，被诊所的人拿棍棒轰了出去在路边休息，张萱萱让刘克去便利店买一包卫生巾，刘克说他一个大男人不好意思。徐如雨让刘克套个黑塑料袋，刘克把塑料袋套头上去了【熊大便利店】，店员以为他打劫，给他一包卫生巾，刘克还不忘要了小票。"),_c('br'),_c('br'),_vm._v(" 9点30，段大鹏黏上卫生巾。老奶奶说她有些头疼，就不陪大家了。赵大鹏拦了计程车说:“我奶头疼，这里最好的医院是哪个？”司机看他五大三粗的，嘴里胡言乱语，几个人都一身酒气，不敢拉他们，便说了个他下班了，前边坐地铁2站路就是【蓝海医院】"),_c('br'),_c('br'),_vm._v(" 老奶奶说她不用去医院，回去睡一觉就好了。大家便坐在路边聊天,。"),_c('br'),_c('br'),_vm._v(" 9点40，马晓光尿急去找厕所，看到一家包子店门口放了个桶，两个小男孩在那里撒尿，桶上写“童子尿”，马晓光心想自己还是童子之身，便也尿了一泡在里边，被老板追着打出来，头也打破了。马晓光一摸头上有血，他说自己快死了，大家便陪着马晓光去坐地铁。"),_c('br'),_c('br'),_vm._v(" 9点50，大家上了地铁，买了5张票，徐如雨掏的100元钱，给找了90个钢铺。车上有人给孩子喝可乐（倒在儿童水壶里），不小心撒了一点，地上有泡沫，刘克看到后抢过水壶撒在地上，义正言辞的说:“这有毒!”被地铁安全员教育，发了个朋友圈，说自己在地铁上捣乱，集满40个赞才能离开。"),_c('br'),_c('br'),_vm._v(" 10点15分，大家去了医院，医生说马晓光只是擦破了皮，没事，贴个创可贴就好了。而这时段大鹏因为失血感觉头晕，医生批评了他们，卸掉卫生巾重新包扎了段大鹏的大腿。并嘱咐回去喝点排骨汤补补。"),_c('br'),_c('br'),_vm._v(" 10点40分，大家出来，都说饿了，徐如雨找了间能做饭的民宿。去的路上马晓光把一辆电单车（因为他平时是代驾）扛了上去，说兄弟平时我总骑你，今天你也骑我一回。"),_c('br'),_c('br'),_vm._v(" 莫丽和张萱萱则去永丰超市买了骨头和萝卜准备熬汤，买了些土豆，给马晓光带了创可贴。"),_c('br'),_c('br'),_vm._v("11点，段大鹏说萝卜要切成丁才好。（莫丽把萝卜切成了“丁”）马晓光让莫丽帮他贴创可贴，莫丽说没时间，让他找别人，他找了徐如雨，徐如雨说写诗呢，让他对着镜子贴创可贴，都贴在了镜子上，徐如雨写下了诗“愿以笔杆化神剑 ，斩破苍穹怒问天，前生犯了什么错，使我今生没人怜？”徐如雨想吃苹果，把土豆啃成了苹果核状。"),_c('br'),_c('br'),_vm._v(" 11点15分，发现灶具打不着火（没有气），大家都说去外边吃吧。张萱萱拿了电视遥控器以为是手机。 11点25分，在电线杆上看到贴的寻人启事（自己的哥哥精神分裂找到后联系某电话号码其实是何子安的下线“鬼哥”），徐如雨顺手撕下来就放在了包里。"),_c('br'),_c('br'),_vm._v(" 11点30分，几个人到了小吃店回马枪餐馆，刘克想尿尿，问店员卫生间，店员说在后边，刘克回来后说卫生间有鬼，一开门就自己亮灯，还有寒气袭来，其实他尿在了冰箱里。马晓光拿着一只小龙虾给了刘克，说好歹有个龙字，带上驱驱邪。"),_c('br'),_c('br'),_vm._v(" 11点45分，几个人吃饭，段大鹏说饭店的排骨汤真好喝，菜也好吃，非要认饭店老板当干爹，还加了微信（老头微信名是:宝刀未老（朋友圈发的回马枪美食店秋季小龙虾折扣季开始了!）。这时看到有人进来坐在一个小角落吃饭（这个人就是鬼哥，他在和买毒品的人接头，何子安为了避免鬼哥见到自己,提前藏进了厕所,张萱萱拿遥控器打电话打不通，几个人硬是把鬼哥送进了派出所）"),_c('br'),_c('br'),_vm._v(" 鬼哥为了毁灭证据，把装有毒品的哆啦A梦扔在了饭店角落被徐如雨捡到换了关公。"),_c('br'),_c('br'),_vm._v(" 12点30分，马晓光出门看到一辆出租车停在路边，他让司机带着鬼哥去警察局，鬼哥说快放开我,否则我叫兄弟杀你全家，司机害怕惹事，不敢开车，大家说这是精神分裂的症状，段大鹏把司机拉下来，让马晓光上去发动了车，和刘克一起把鬼哥送到了警察局。（得了2个好市民锦旗）"),_c('br'),_c('br'),_vm._v(" 1点10分，马晓光和一个法拉利飚车。以漂移的方式在十字路口掉头，被一帮社会青年称作车神。说要带去见大哥，给马晓光在美发厅做了一个葬爱帮的朋克发型。给刘克在纹身店做了一个葬爱无敌的纹身。"),_c('br'),_c('br'),_vm._v(" 葬爱老大让马晓光开车压死对头魅影家族一头（以飚车作为胜负赢一只很珍稀的金刚黑琵琶），然后马晓光胜利，对方给马晓光一个箱子。里边装着装蝎子的笼子。说你的车技果然了得，东西算你们的。"),_c('br'),_c('br'),_vm._v(" 刘克说这个蝎子好漂亮，一摸口袋，有个小龙虾是之前马晓光给刘克拿的。他拿小龙虾换了蝎子。"),_c('br'),_c('br'),_vm._v("马晓光把东西给了葬爱老大。因为天黑，老大没注意，夸了马晓光几句，还给了他2000元奖励。马晓光和刘克对半分了。"),_c('br'),_c('br'),_vm._v(" 2点，马晓光和刘克回来了。马晓光给了司机500元车辆使用费，司机高兴的走了。刘克把蝎子放在了小龙虾的盆子里说养着。"),_c('br'),_c('br'),_vm._v(" 2点30分，大家离开小饭馆，刘克说坏了，蝎子忘在饭馆小龙虾盆里了，段大鹏连忙给老头打电话:干爹，我朋友旁边放的那盆小龙虾还在不?老头说他去看看去。过了10分钟回微信:你妈炸了!"),_c('br'),_c('br'),_vm._v(" 2点45分，路过名表店，莫丽说最喜欢劳力士了，刘克说难得赚了点钱，买个劳力士去，问老板1000能买个什么劳力士？老板看他们一身酒气，说1000我给你画一个。给莫丽手腕上画了一个劳力士图案。"),_c('br'),_c('br'),_vm._v(" 3点，莫丽说这表也不走，一直都是2点50分，要在典当行当了，被典当行的人撵出来了。"),_c('br'),_c('br'),_vm._v(" 3点15分，莫丽看到一个美容院，她最恨这个，要买炸弹炸美容院。于是徐如雨在小卖部买了副扑克，拿走了所有的炸弹牌（4个一组)，放在了美容院里，然后张萱萱让段大鹏打电话给110（因为张萱萱的电话是电视遥控器），说有人在美容院放了13个炸弹。"),_c('br'),_c('br'),_vm._v(" 3点30分，之前和鬼哥联络买毒品的人找到了大家，拿刀逼着他们交出哆啦A梦，何子安怕这些人破坏自己的计划，他刚好身上有带一些毒品，便出面说明了自己的身份，给了那些人毒品支走了他们（所以何子安包里的毒品少了）"),_c('br'),_c('br'),_vm._v(" 何子安在机关布置的石头要砸下来之前，对着siri说:告诉张萱萱，说我爱她。被Siri理解错误，发给了张萱萱的手机“我爱她”。其实何子安的意思是他爱张萱萱。"),_c('br'),_c('br')])],1):_c('van-button',{staticClass:"btn",attrs:{"color":"#FFB700","round":""},on:{"click":function($event){return _vm.levelup(6)}}},[_vm._v("解锁真相")])],1)],1)}
